<template>
  <div class="hello">
    java
    <div @click="goJava('http://www.vuece.com/mnt')">mnt</div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    goJava (link) {
      var history = window.history
      history.pushState({ name: 'a' }, link, link)
    }
  }
}
</script>

